import './InnerLink.scss';

import React from 'react';
import { Link, } from './Link';



export default function InnerLink({ children, to, text, }) {
  if (text)
    return (
      <Link to={to} className="inner__link">{text}</Link>
    );

  return (
    <Link to={to} className="inner__link">{children}</Link>
  );
};
